<template>
	<div style="padding:20px;">
      <Button @click='handleGoAdd' type='primary'>
        新增优惠券
      </Button>
      <div style="margin-top:20px;">
          <!--<Tabs size="small" type="card" :value='page.activeTab' @on-click='handleTabChange'>-->
            <!--<TabPane label="全部" name='0'></TabPane>-->
            <!--&lt;!&ndash; <TabPane label="已超时" name='1'></TabPane>-->
            <!--<TabPane label="未分配" name='3'></TabPane>-->
            <!--<TabPane label="已取消" name='2'></TabPane> &ndash;&gt;-->
          <!--</Tabs>-->
          <Table :loading="loading" ref="appointment" :columns="columns" :data="listData" no-data-text='暂无优惠券' border></Table>
          <div style="margin-top: 15px;overflow: hidden">
            <div style="float: right;">
              <Page :total="totalcount" :page-size="page.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange' :current="page.pageindex+1"></Page>
            </div>
          </div>
        </div>
      <Modal  v-model="showConfirm" title="确认提示" @on-ok="updateConfirm" width="300">
            <div v-if="!!showText">{{showText}}</div>
            <div v-else>
                <span>投放时长:</span>
                <InputNumber  style="width:150px" v-model="updateForm.expiretimes" :min="1" :step="1"></InputNumber>
            </div>
      </Modal>
      <Modal
      v-model="loadBol"
      title='小程序码(优惠券)'>
        <div style="text-align:center;">
            <img :src='wxImg'  id='myCanvas' style="width:200px;height:200px"/>
        </div>
        <div slot="footer">
            <Button type="primary" @click="down" :long='true'>下载</Button>
        </div>
      </Modal>
	</div>
</template>
<script >
export default {
  data() {
    return {
      page: {
        pagesize: 10,
        pageindex: 0,
        activeTab: "0"
      },
      loading: false,
      columns: [],
      listData: [],
      totalcount: 0,
      //优惠券类型的map表
      typeMap: [
        {
          key: 1,
          value: "满减券"
        },
        {
          key: 2,
          value: "代金券"
        },
        {
          key: 3,
          value: "月券"
        }
      ],
      //适用人群的map表
      userMap: [
        {
          key: 1,
          value: "所有用户"
        },
        {
          key: 2,
          value: "新注册用户"
        }
      ],
      //优惠券优惠方式map表
      modelMap: [
        {
          key: 1,
          value: "抵扣"
        },
        {
          key: 2,
          value: "折扣",
          disabled: true
        }
      ],
      //场景map
      sceneMap: [
        // {
        //   key: 1,
        //   value: "注册返券"
        // },
        // {
        //   key: 2,
        //   value: "购买券"
        // },
        // {
        //   key: 3,
        //   value: "下单返券",
        //   disabled: true
        // },
        // {
        //   key: 4,
        //   value: "点击领券",
        //   disabled: true
        // }
        {
          key: 5,
          value: "会员礼包"
        },
        {
          key: 6,
          value: "扫码领券"
        }
      ],
      //使用范围map
      scopeMap: [
        {
          key: 1,
          value: "全场通用"
        },
        {
          key: 2,
          value: "部分品类"
        },
        {
          key: 3,
          value: "部分商品",
          disabled: true
        }
      ],
      expiretimes: 1,
      showConfirm: false,
      showText: "",
      updateForm: {
        expiretimes: 1
      },
      wxImg: "",
      loadBol: false
    };
  },
  mounted() {
    this.$sitemap.prvname = "优惠券";
    this.$sitemap.currname = "优惠券列表";
    this.$menuactive.open = ["SalesBlock"];
    this.$menuactive.active = "CouponList";
    this.getCouponList();
    this.initHeader();
  },
  watch: {
    page: {
      handler(newval) {
        this.getItemList();
      },
      deep: true
    }
  },
  methods: {
    handleGoAdd() {
      this.$router.push({ name: "CouponAdd" });
    },
    getCouponList() {
      this.$http
        .post(this.$api.coupon_list, this.page)
        .then(res => {
          if (res.data.result == "1") {
            this.listData = res.data.data;
            this.totalcount = +res.data.totalcount;
          } else {
            that.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          that.$Message.error("服务器异常");
        });
    },
    //分页组件size和index变化的handler
    indexChange(index) {
      this.page.pageindex = index - 1;
    },
    sizeChange(size) {
      this.page.pagesize = size;
    },
    initHeader() {
      this.columns = [
        {
          title: "优惠券名称",
          key: "couponname",
          align: "center",
          minWidth: 200
        },
        {
          title: "优惠券副标题",
          key: "remark",
          align: "center",
          minWidth: 200
        },
        {
          title: "类型",
          key: "coupontype",
          align: "center",
          width: 100,
          render: (h, params) => {
            let filter = this.typeMap.find(
              it => it.key == params.row.coupontype
            );
            return h("span", filter ? filter.value : "-");
          }
        },
        {
          title: "优惠金额",
          key: "cutprice",
          align: "center",
          width: 100
        },
        {
          title: "领券方式",
          key: "couponscene",
          align: "center",
          width: 100,
          render: (h, params) => {
            let filter = this.sceneMap.find(
              it => it.key == params.row.couponscene
            );
            return h("span", filter ? filter.value : "-");
          }
        },
        {
          title: "适用用户",
          key: "usertype",
          align: "center",
          width: 100,
          render: (h, params) => {
            let filter = this.userMap.find(it => it.key == params.row.usertype);
            return h("span", filter ? filter.value : "-");
          }
        },
        {
          title: "投放状态",
          key: "isissued",
          align: "center",
          width: 100,
          render: (h, params) => {
            return h(
              "span",
              {
                style: {
                  color: "#3bc0c3"
                }
              },
              params.row.isissued == 1 ? "已投放" : "未投放"
            );
          }
        },
        {
          title: "操作",
          key: "operate",
          align: "center",
          width: 150,
          render: (h, params) => {
            let issuedBtn = h(
              "Button",
              {
                props: {
                  type: "primary",
                  size: "small"
                },
                on: {
                  click: () => {
                    this.showConfirm = true;
                    this.showText =
                      params.row.isissued != 1 ? "" : "确认停止投放吗?";
                    if (!!params.row.isissued) {
                      this.updateForm = {
                        merid: params.row.merid,
                        couponid: params.row.id,
                        isissued: 0,
                        expiretimes: 0
                      };
                    } else {
                      this.updateForm = {
                        merid: params.row.merid,
                        couponid: params.row.id,
                        isissued: 1,
                        expiretimes: 1
                      };
                    }
                  }
                }
              },
              params.row.isissued != 1 ? "投放" : "停止"
            );
            let loadWxBtn = "";
            if (params.row.couponscene == 6) {
              loadWxBtn = h(
                "Button",
                {
                  props: {
                    type: "ghost",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.loadFile(params.row.id);
                    }
                  },
                  style: {
                    marginLeft: "5px"
                  }
                },
                "分享码"
              );
            }
            return h("div", [issuedBtn, loadWxBtn]);
          }
        }
      ];
    },
    //确认操作
    updateConfirm() {
      let that = this;
      let url = that.$api.coupon_editisissued;
      that.$http
        .post(url, that.updateForm)
        .then(res => {
          if (res.data.result == 1) {
            that.$Message.success("更新成功!");
            that.getCouponList();
          } else {
            that.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          that.$Message.error("服务器异常");
        });
    },
    //文件下载
    loadFile(id) {
      let params = {
        path: "pages/receive/index",
        couponid: id
      };
      this.$Message.loading({
        content: "加载中...",
        duration: 10
      });
      this.$http
        .post(this.$api.wxa_getqrcode, params)
        .then(res => {
          if (res.data.result) {
            this.wxImg = res.data.data;
            this.loadBol = true;
            this.$Message.destroy();
          } else {
            this.$Message.error(res.data.msg);
          }
        })
        .catch(() => {
          this.$Message.error("服务器异常");
        });
    },
    down() {
      // 生成一个a元素
      let a = document.createElement("a");
      // 创建一个单击事件
      let event = new MouseEvent("click");
      // 将a的download属性设置为我们想要下载的图片名称，若name不存在则使用‘下载图片名称’作为默认名称
      a.download = "分享码";
      // 将生成的URL设置为a.href属性
      a.href = this.wxImg;
      // 触发a的单击事件
      a.dispatchEvent(event);
    }
  }
};
</script>



